import React from "react";
import Content from "components/Content/Content.component";
import Container from "components/Container/Container.component";
import Wrapper from "components/Wrapper/Wrapper.component";
import Heading from "components/Heading";
import Text from "components/Text";
import Navbar from "components/Navbar/Navbar.component";
import styles from "./Home.module.scss";

const Home = () => {
  return (
    <>
      <Navbar />
      <div className={styles.imgWrapper}>
        <Content>
          <Container>
            <Wrapper>
              <div className={styles.box}>
                <Wrapper>
                  <Heading level={1} size="l">
                    Vlkommen till Oakflowers!
                  </Heading>
                  <Text color="text-secondary">
                    Det kan hnda att hemsidan ligger nere eller har olika
                    problem under en kortare stund p grund av uppdatering av
                    servern. Vid fel under en lngre period, vnligen kontakta
                    oss.
                  </Text>
                  <Text>
                    Vi r ven tacksamma fr all feedback s hr grna av er om
                    frbttringar eller ifall ngot inte fungerar!
                  </Text>
                </Wrapper>
              </div>
            </Wrapper>
          </Container>
        </Content>
      </div>
    </>
  );
};

export default Home;
